/* MEDIA */

@media (max-width: 1000px){
    .border{

        padding-left: 0%;
        padding-right: 0%;
        max-width: 100%;
    }
    .someNav{ 
        padding-left: 0%;
        padding-right: 1%;
    }
    .play{
        height: 15px;;
    }
    .feedback p{
        filter: alpha(Opacity=0); /* Полупрозрачность для IE */
        opacity: 0.0;
    }
    .bottomBlockbox-2box-3 p, .bottomBlockbox-2box-4 p, .bottomBlockbox-2box-5 p, .bottomBlockbox-2box-6 p, {
        display: inherit;
    }
    .bottomBlockbox-2box-3 img, .bottomBlockbox-2box-4 img, .bottomBlockbox-2box-5 img, .bottomBlockbox-2box-6 img, {
        display: inherit;

    }
    .addText{
        display: inherit;
        
    }
    .feedback{
        display: none;
    }
    
}
